import React, { useState, useEffect, useRef } from 'react';
import Modal from 'react-bootstrap/Modal';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { useForm } from 'react-hook-form';
import { getProfanity, updateProfanity } from '../../../../../actions/appsMoreAction';
import { TOAST_ERROR, TOAST_SUCCESS, TOAST_LOADING, TOAST_DISMISS } from "../../../../../utils";
import { HOST_URL } from "../../../../../actions/api";

const Profanity = ({ showProfanity, closeProfanity, getData }) => {

    const [downloadSample, setDownloadSample] = useState(0);
    const { register, handleSubmit, clearErrors, formState: { errors }, reset, setValue } = useForm();

    const [wallId, setWallId] = useState(getData.detail?.wallId || '');
    const [ownerId, setOwnerId] = useState(getData.detail?.wallOwner || '');

    const myRefname = useRef(null);
    const [profanityDefaultUrl, setprofanityDefaultUrl] = useState('');
    const [profanityUrl, setprofanityUrl] = useState('');
    const [profanityFileName, setprofanityFileName] = useState('');
    const [profanityFile, setprofanityFile] = useState('');
    const [profanityStatus, setprofanityStatus] = useState(0);

    const [profanityForm, setprofanityForm] = useState(0);
    const [spinner, setspinner] = useState(false);

    const profanityFilter = () => {

        const formData = new FormData();

        formData.append('profanityFile', profanityFile);
        formData.append('ownerId', ownerId);
        formData.append('wallId', wallId);
        formData.append('status', profanityStatus);
        formData.append('profanity_url_link', profanityUrl);

        if (downloadSample == 2) formData.append('useruploadProfanity', 1);
        if (downloadSample != 2) formData.append('defaultProfanity', 1);


        let fileInput = document.getElementById('fileupload').value;
        let allowedExtenstion = /(\.csv|\.xls|\.CSV|\.XLS)$/i;

        if (!allowedExtenstion.exec(fileInput) && fileInput.length > 0 && downloadSample != 1) {
            fileInput = '';
            TOAST_ERROR("Only csv & xls file allowed");
            return false;
        } else {
            TOAST_LOADING();
            setspinner(true);
            updateProfanity(formData).then((response) => {
                TOAST_DISMISS();
                if (response.data.status == true) {
                    TOAST_SUCCESS(response.data.message);
                    closeProfanity();
                } else {
                    TOAST_ERROR(response.data.message);
                }
                setspinner(false);
            }).catch((error) => {
                TOAST_DISMISS();
                TOAST_ERROR(error.message);
            })
        }
    }


    const FileChange = (e) => {
        setprofanityFile(e.target.files[0]);
        setprofanityFileName(e.target.files[0]['name']);
        setprofanityUrl(e.target.files[0]['name']);
        setDownloadSample(2);
    }

    const getProfanityData = () => {
        // TOAST_LOADING();
        getProfanity(ownerId, wallId).then((response) => {
            if (response.data.status == true) {
                const res = response.data.response;
                setprofanityDefaultUrl(`https://app.taggshop.io/${response.data.default_link}`);
                setprofanityUrl(res.profanity_url);
                setprofanityFileName(response.data.filename);
                setprofanityStatus(res.profanity);
                setDownloadSample(res.profanityUse ? 2 : 1);
                setprofanityForm(1);
                // TOAST_DISMISS();
            }
        })
    }

    useEffect(() => {
        getProfanityData();
    }, [showProfanity]);


    return (
        <>
            {profanityForm ?
                <form onSubmit={handleSubmit(profanityFilter)} action="" className="maintainPopUp noLoader hc-45" method="post" acceptCharset="utf-8">
                    <div className="body-bg rightInner_wrap h-100 d-flex flex-column justify-content-between">
                        <><div className="row">
                            <div className="col-md-12">
                                <div className="tabbable tabbable-tabdrop">
                                    <div className="tab-content profanityStats mt-3">
                                        <div className="tab-pane active">
                                            <div className="form-group">
                                                <p className="lightTxtPera">Turn on this option to auto filter the content based on a pre-set list of unwanted words.</p>
                                            </div>
                                        </div>
                                        <div className="tab-pane active profanityKey">
                                            <div className="portlet-body">
                                                <div className="profanity_wrap">
                                                    <div className="checkbox-inline d-flex justify-content-between align-items-center w-100">
                                                        <label key={downloadSample} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary mr-2">
                                                            <input disabled={downloadSample != 2 ? true : false} onClick={(e) => setDownloadSample(e.target.checked ? 1 : 0)} type="checkbox" defaultChecked={downloadSample != 2 ? true : false} /><span></span>Use our predefined vulgar word list
                                                        </label>
                                                        <a href={profanityDefaultUrl} className="btn btn-icon btn-icon-mobile btn-clean" download="">
                                                            <span className="svg-icon svg-icon-2x">
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                    <g data-name="Group 4422" transform="translate(10979 -4685)">
                                                                        <rect data-name="Rectangle 2094" width="24" height="24" transform="translate(-10979 4685)" fill="none" opacity="0"></rect>
                                                                        <g data-name="Group 4410" transform="translate(-10979 4685)">
                                                                            <path
                                                                                data-name="Path 5217"
                                                                                d="M11.291,15.4a.989.989,0,0,0,.7.283,1.108,1.108,0,0,0,.776-.331L15.9,12.209a1.016,1.016,0,1,0-1.436-1.437L13.02,12.217V4.057A1.016,1.016,0,0,0,12,3.036a1.027,1.027,0,0,0-1,1.02v8.172l-1.457-1.46a1.038,1.038,0,0,0-1.436,0,1.014,1.014,0,0,0,0,1.436Z"
                                                                                fill="#545454"
                                                                            ></path>
                                                                            <path data-name="Path 5218" d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z" fill="#545454"></path>
                                                                        </g>
                                                                    </g>
                                                                </svg>
                                                            </span>
                                                        </a>
                                                    </div>
                                                    <div className="d-flex align-items-center my-3">
                                                        <div className="separator separator-solid col"></div>
                                                        <div className="symbol symbol-circle">
                                                            <span className="symbol-label">OR</span>
                                                        </div>
                                                        <div className="separator separator-solid col"></div>
                                                    </div>

                                                    <div className="profanityCols" >
                                                        <a onClick={(e) => { myRefname.current.click() }} className="btn default upload-area">
                                                            <span className="glyphicon glyphicon-level-up"></span>
                                                            <p>Upload your own File</p>
                                                            <input ref={myRefname} id='fileupload' accept=".csv" onChange={(e) => FileChange(e)} type="file" name="profanityFile" style={{ display: 'none' }}></input>
                                                        </a>

                                                        {profanityFileName ?
                                                            <div className="checkbox-inline justify-content-between align-items-center w-100 mt-4">
                                                                <label key={downloadSample} className="checkbox checkbox-outline checkbox-outline-2x checkbox-primary me-2">
                                                                    <input disabled={downloadSample == 2 ? true : false} onClick={(e) => setDownloadSample(e.target.checked ? 2 : 0)} defaultChecked={downloadSample == 2 ? true : false} type="checkbox" name="useruploadProfanity" value="1" />
                                                                    <span></span>
                                                                    <p className="showFileName m-0">{profanityFileName}</p>
                                                                </label>
                                                                <a href={profanityUrl} className="btn btn-icon btn-icon-mobile btn-clean" download="">
                                                                    <span className="svg-icon svg-icon-2x">
                                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                            <g data-name="Group 4422" transform="translate(10979 -4685)">
                                                                                <rect data-name="Rectangle 2094" width="24" height="24" transform="translate(-10979 4685)" fill="none" opacity="0"></rect>
                                                                                <g data-name="Group 4410" transform="translate(-10979 4685)">
                                                                                    <path
                                                                                        data-name="Path 5217"
                                                                                        d="M11.291,15.4a.989.989,0,0,0,.7.283,1.108,1.108,0,0,0,.776-.331L15.9,12.209a1.016,1.016,0,1,0-1.436-1.437L13.02,12.217V4.057A1.016,1.016,0,0,0,12,3.036a1.027,1.027,0,0,0-1,1.02v8.172l-1.457-1.46a1.038,1.038,0,0,0-1.436,0,1.014,1.014,0,0,0,0,1.436Z"
                                                                                        fill="#545454"
                                                                                    ></path>
                                                                                    <path data-name="Path 5218" d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z" fill="#545454"></path>
                                                                                </g>
                                                                            </g>
                                                                        </svg>
                                                                    </span>
                                                                </a>
                                                            </div>
                                                        : null}
                                                    </div>

                                                    <div className="form-group form-s formFtr">
                                                        <div className="d-flex align-items-center justify-content-between">
                                                            <div>
                                                                <label htmlFor="automoderationprofanityfilter">
                                                                    Enable Profanity Filter
                                                                    <OverlayTrigger overlay={<Tooltip>This will make all the posts having inappropriate or vulgar words to get disapproved automatically. You can find them later in Private Posts Section in Moderation.</Tooltip>}>
                                                                        <span className="svg-icon svg-icon-sm svg-icon-dark info-detail" data-toggle="tooltip" data-theme="dark">
                                                                            <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                                                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                                                    <rect x="0" y="0" width="24" height="24"></rect>
                                                                                    <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"></circle>
                                                                                    <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"></rect>
                                                                                    <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"></rect>
                                                                                </g>
                                                                            </svg>
                                                                        </span>
                                                                    </OverlayTrigger>
                                                                    <span className="errorMessage"></span>
                                                                </label>
                                                            </div>
                                                            <div className="toggleOnBut">
                                                                <div className="onoffswitch">
                                                                    <span className="switch justify-content-center">
                                                                        <label htmlFor="profanityStatusNew">
                                                                            <input type="checkbox" name="profanity" value="1" id="profanityStatusNew" className="onoffswitch-checkbox updateStatus" data-off-color="#989898" data-on-color="#009385" checked={profanityStatus} onChange={(e) => setprofanityStatus(e.target.checked ? 1 : 0)}/>
                                                                            <span></span>
                                                                        </label>
                                                                    </span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer px-0">
                            <div className="d-flex align-items-center justify-content-end w-100">
                                <div className="card-toolbar">
                                    <button onClick={(e) => closeProfanity()} type="button" className="btn btn-secondary mr-4" data-bs-dismiss="modal">Cancel</button>
                                    <button type="submit" className={`btn btn-primary addMoreAnnocBtn ladda-button  ${spinner ? `spinner spinner-white spinner-right disabled` : null}`}>Apply</button>
                                </div>
                            </div>
                        </div>
                        </>
                    </div>
                </form>
                :
                null
            }
        </>
    );
};

export default Profanity;
