import React, { useState,useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import * as Icon from 'react-bootstrap-icons'; 
import Select from 'react-select';
import { SWAL_FIRE,TOAST_ERROR, TOAST_SUCCESS,TOAST_LOADING,TOAST_DISMISS } from "../../../../../utils";
import { imageRequestCreate,imageDownload,imageRequestList }  from '../../../../../actions/appsMoreAction';
import { useForm } from 'react-hook-form';
import { CLOUDPATH_TAGGSHOP, HOST_URL } from "../../../../../actions/api";
import $ from 'jquery';
import { useDispatch } from 'react-redux';
import { togglePriceSection } from '../../../../../actions/show_modal_Action';

const DownloadImage = ({showdwonloadImage,closedwonloadImage,getData}) => {  
  const dispatch = useDispatch()

    const [ownerId, setOwnerId] = useState(getData.detail?.wallOwner || '');
    const [downloadStatus, setdownloadStatus] = useState(getData.detail?.wall.User.UserRule.download_img || '');

    const [downloadImage, setdownloadImage] = useState(0);
    const [spinner,setSpinner]= useState(false);

    const { register, handleSubmit,clearErrors, formState: { errors },reset,setValue } = useForm();

    const [downloadImageForm, setdownloadImageForm] = useState(0);
    const [defaultPopup, setdefaultPopup] = useState(0);

    const [downloadWall, setDownloadWall] = useState([]);
    const [downloadRequest, setDownloadRequest] = useState('');
    const [downloadwallId, setdownloadwallId] = useState();

    const [download , setDownload]  =useState(null);
    const onUpgradeClick = (e) => {
      dispatch(togglePriceSection(true, 2))
    }
  
    
    const onCustomPostSubmit=()=>{
       setSpinner(true);
       TOAST_LOADING();

         imageRequestCreate(ownerId,downloadwallId).then((response)=>{ 

             TOAST_DISMISS();
              
            if(response.status==200 && response.data.status==true){ 
 
               TOAST_SUCCESS(response.data.message);
               closedwonloadImage()
                 }else{
               TOAST_ERROR(response.data.message);
            }  

           setSpinner(false);
      }).catch((error)=>{
          TOAST_DISMISS();
          TOAST_ERROR(error.message);
      }) 
   }
    

   const getDownloaddata=()=>{

      TOAST_LOADING();

        imageDownload(ownerId).then((response)=>{  
            if(response.status==200 && response.data.status==true){ 

                response && response.data.response.map((item, Index) => {
                  downloadWall.push({ label : item.name, value : item.id })
              })

              setdownloadwallId(response && response.data.response ? downloadWall[0].value : "");
           }  
       }) 

        imageRequestList(ownerId).then((response)=>{   
          if(response.status==200 && response.data.status==true){
            setDownloadRequest(response.data.response);
            setdownloadImageForm(1);
            setdefaultPopup(1);
            TOAST_DISMISS();
        }  
     })
   }

      
    useEffect(() => { 

      if(downloadStatus){
         getDownloaddata();
        if(showdwonloadImage==false){
          setdownloadImage(0);
       }
     }  

    }, [showdwonloadImage]);
    

  return (
    <>

 {downloadStatus ?

  defaultPopup ?    downloadImageForm ?
    
    downloadImage==0 && downloadRequest ?

      <div className="customPostSec postcWrapC">
         <div className="wallcreateshow">
            <div className="portlet-body rightInner_wrap"> 

              <div className="d-flex justify-content-end">
                <div onClick={(e)=>setdownloadImage(1)} className="commanBtn btn-sm btn btn-primary dwnldImgsBtn mb-0">Add New</div>	
              </div>

              <label className="columnTitle"></label>
                <div className="table-scrollable table-scrollable-borderless globalTblList mt0">
                       <table className="table table-hover">
                                <tbody>
                                    <tr className="uppercase text-center" align="center">
                                        <th align="left" style={{ textAlign:'left'}}>Wall Name</th>
                                        <th>Status</th>
                                        <th>Expire on</th>
                                        <th>Action</th>
                                    </tr>
                                </tbody>
                                <tbody>
                                    { downloadRequest.map((data)=>{
                                      return(<tr   className="text-center" align="center">
                                          <td className="authorPicTd text-left"> {data.wall?.name || ''}</td>
                                          <td>
                                              { data.download_complete==1 ? <font color='green'>Completed</font> 
                                              : <font color="orange">In Progress</font> } 
                                          </td>
                                          <td>
                                            { data.download_complete==1 ? data.download_cron_time: <font color="orange">-</font>}
                                          </td>
                                          
                                          <td className="text-center">
                                            <a href={`${data.download_complete==1 ? `${HOST_URL}/wallimages/archive/${data.foldername}.zip?v=${Math.floor(Math.random()*100000)}` : "javascript:void(0)" }`} 
                                            className={`btn btn-default btn-sm tooltips ${data.download_complete==1 ? 'readyDwnld' : null} `} data-original-title="Download">
                                            {/* <i className="fa fa-download"></i> */}
                                            <span className="svg-icon svg-icon-2x me-0">
                                              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                <g id="Group_4422" data-name="Group 4422" transform="translate(10979 -4685)">
                                                  <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10979 4685)" fill="none" opacity="0"></rect>
                                                  <g id="Group_4410" data-name="Group 4410" transform="translate(-10979 4685)">
                                                    <path id="Path_5217" data-name="Path 5217" d="M11.291,15.4a.989.989,0,0,0,.7.283,1.108,1.108,0,0,0,.776-.331L15.9,12.209a1.016,1.016,0,1,0-1.436-1.437L13.02,12.217V4.057A1.016,1.016,0,0,0,12,3.036a1.027,1.027,0,0,0-1,1.02v8.172l-1.457-1.46a1.038,1.038,0,0,0-1.436,0,1.014,1.014,0,0,0,0,1.436Z" fill="#545454"></path>
                                                    <path id="Path_5218" data-name="Path 5218" d="M21.083,18H2.917a.99.99,0,0,0-.984.982v.006a1,1,0,0,0,.985.981H21.083a.985.985,0,0,0,0-1.969Z" fill="#545454"></path>
                                                  </g>
                                                </g>
                                              </svg>
                                            </span>
                                              </a>
                                          </td>
                                      </tr>)
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
             </div>
         
          : 

         
  <form onSubmit={handleSubmit(onCustomPostSubmit)}  className="maintainPopUp noLoader downloadImage_select_wall" method="post" acceptCharset="utf-8">
    <div className="downloadImgColm">
      <div className="customPostSec postcWrapC rightInner_wrap">
        <div className="col-md-12 wallcreateshow h-100">
            <div className="portlet-body rightInner_wrap h-100">

              { downloadRequest ? 
                <ul className="nav nav-tabs justify-content-end">
                    <li style={{ float:'left'}} className="">
                        <div onClick={(e)=>setdownloadImage(0)}  title="create" data-theme="dark" data-bs-toggle="tooltip" className="commanBtn btn btn-sm btn-primary popUp maintainPopUp creatNewPostBtn tooltips" data-popup-width="data-width='750px'"><Icon.ChevronLeft/> Back</div>
                    </li>
                </ul> : null }
                   
                  <div className="downldSlctWalBlock downloadImages_wrap h-100">
                    <div className="row h-100 d-flex align-items-center">
                      <div className="col-md-12 col-sm-12">
                        <div className="form-group">			 
                          <label htmlFor="" className="columnTitle">
                            Select Wall
                          </label>
                            <div className="input-group flex-nowrap">
                              <div className="input-group-prepend">
                                  <span className="input-group-text">
                                    <span className="svg-icon svg-icon-sm">
                                      <svg xmlns="http://www.w3.org/2000/svg"  width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                          <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                              <rect x="0" y="0" width="24" height="24"></rect>
                                              <rect fill="#000000" x="4" y="4" width="7" height="7" rx="1.5"></rect>
                                              <path d="M5.5,13 L9.5,13 C10.3284271,13 11,13.6715729 11,14.5 L11,18.5 C11,19.3284271 10.3284271,20 9.5,20 L5.5,20 C4.67157288,20 4,19.3284271 4,18.5 L4,14.5 C4,13.6715729 4.67157288,13 5.5,13 Z M14.5,4 L18.5,4 C19.3284271,4 20,4.67157288 20,5.5 L20,9.5 C20,10.3284271 19.3284271,11 18.5,11 L14.5,11 C13.6715729,11 13,10.3284271 13,9.5 L13,5.5 C13,4.67157288 13.6715729,4 14.5,4 Z M14.5,13 L18.5,13 C19.3284271,13 20,13.6715729 20,14.5 L20,18.5 C20,19.3284271 19.3284271,20 18.5,20 L14.5,20 C13.6715729,20 13,19.3284271 13,18.5 L13,14.5 C13,13.6715729 13.6715729,13 14.5,13 Z" fill="#000000" opacity="0.3"></path>
                                          </g>
                                      </svg>
                                      </span>
                                </span>
                             </div>

                              <Select className="w-100" onChange={(e)=>setdownloadwallId(e.value)} styles={{width:'100%'}} options={downloadWall} defaultValue={downloadWall[0]} />

                           </div>

                              { !downloadwallId ?  <div>
                                <input  {...register("wall", { required: true, required: 'This field is required.'})}  type="hidden" defaultValue={''}/> 
                                {errors.wall  && <span className="text-error">{errors.wall.message}</span>} </div>
                              : null }
                        </div>
                      </div>
                    </div>
                  </div>
                </div>		
                <div className="form-group mb0 formFtr">
                  <div className="px-9 text-center">
                    <span  className="errorMessage">
                    </span>
                  </div>
                </div>
             </div>
        </div>
    </div>


    <div className="modal-footer">
      <div className="d-flex align-items-center justify-content-end w-100">
          <div className="card-toolbar">
          <button onClick={(e)=>closedwonloadImage()} type="button" className="btn btn-secondary mr-4" data-bs-dismiss="modal">Cancel</button>
          <button type="submit"  className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : null}`}>Download Images</button>
          </div>
      </div>
    </div>

  </form>  

    :

    <div></div>

   

   : null 

   :  
 
        <div className="customPostSec postcWrapC rightInner_wrap">
          <div className="col-md-12 wallcreateshow h-100">
            <div className="portlet-body h-100"> 
                <div className="modal-body h-100">
                    <div className="row h-100 d-flex align-items-center">
                        <div className="col-lg-12">
                            <div className="upgredeMain download_image text-center py-8">
                                <div className="Errorimage pb-3">
                                    <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/download-image-icon.svg`} />
                                </div>

                                <div className="permissionMsg mt-4 px-4">
                                    <h4 className="ErrorMsg">Download Images</h4>
                                    <p className="msgSec">Download images from your feed and repurpose it on other marketing touchpoints.</p>

                                    <div className="buttonUpgred mb-sm-5 mb-3">
                                        <a href="javascript:;" id="addonFeaturesScroll" onClick={(e)=>onUpgradeClick()} className="commanBtn btn btn-primary px-5 d-inline-block"> Upgrade Now </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
             </div>
           </div>
         </div>   
 
   
   }

   </>
  );
};

export default DownloadImage;
