import React, { useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { addCollaborator, DeleteKeys } from "../../../../../../actions/appsMoreAction";
import { emailValidation, TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS } from "../../../../../../utils";
import { API_URL, CLOUDPATH_TAGGSHOP, HOST_URL } from "../../../../../../actions/api";
//import { useCookies } from "react-cookie";
import $ from 'jquery';
import { togglePriceSection } from "../../../../../../actions/show_modal_Action";
import { useDispatch } from "react-redux";

export default function Collaborator(props) { 
  const [emailError, setEmailError] = useState(false);
  const [value_1, setValue_1] = useState(false)
  const [email, setEmail] = useState({ email: "" });
  const [value, setValue] = useState();
  const [spinner, setspinner] = useState(false);
  var data = `${email.email}`;
  const [Collab, setCollaborator] = useState(null);
  const dispatch = useDispatch()

  const onUpgradeClick = (e) => {
    setTimeout(() => {
      setCollaborator(null);
      window.showpricing(6, 'Widget', 'widget')
      props.onHide()
    }, 100);

    $('#morePopupId').hide()
  }


  const onSubmit = (e) => {
    setEmailError(!emailValidation(data))
    setValue_1(value ? false : true)
    if (!emailValidation(data) || !value) return
    setspinner(true);
    TOAST_LOADING()
    const data_1 = { emailId: email.email, collaboratorRole: value };
    addCollaborator(data_1)
      .then((res) => {
        if (res.status == 200) {
          TOAST_DISMISS()
          TOAST_SUCCESS("Collaborator created successfully.")
          setspinner(false);
          props.getNewData()
          DeleteKeys()
          props.onHide()
        } else {
          TOAST_DISMISS()
          TOAST_ERROR(res.message);
          setspinner(false);
          props.onHide();
        }
      }).catch((res) => {
        TOAST_DISMISS()
        TOAST_ERROR(res.response.data.responseMessage);
        setspinner(false);
        props.onHide();
      })
  };
  function keyDown(e) {
    var e = window.event || e;
    var key = e.keyCode;
    if (key == 32) {
      e.preventDefault();
    }
  }

  return (

    props.collaboratorList.length != props.userData?.UserRule.collaborator && props.userData?.UserRule.collaboratorStatus == 1 ?
      <div className="addCollabrtr rightInner_wrap">
        <div className="portlet-body h-100" id="addCollaboratorBlock">
          <div className="init maintainPopUp noLoader collaborator_inner h-100" id="collaboratorCreatePopUp" >
            <div className="row">
              <div className="col-12">
                <div className="form-group">
                  <label> Enter Email <span>*</span> </label>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text">
                        <span className="svg-icon">
                          <svg xmlns="http://www.w3.org/2000/svg" link="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd" >
                              <polygon points="0 0 24 0 24 24 0 24" />
                              <path d="M18,8 L16,8 C15.4477153,8 15,7.55228475 15,7 C15,6.44771525 15.4477153,6 16,6 L18,6 L18,4 C18,3.44771525 18.4477153,3 19,3 C19.5522847,3 20,3.44771525 20,4 L20,6 L22,6 C22.5522847,6 23,6.44771525 23,7 C23,7.55228475 22.5522847,8 22,8 L20,8 L20,10 C20,10.5522847 19.5522847,11 19,11 C18.4477153,11 18,10.5522847 18,10 L18,8 Z M9,11 C6.790861,11 5,9.209139 5,7 C5,4.790861 6.790861,3 9,3 C11.209139,3 13,4.790861 13,7 C13,9.209139 11.209139,11 9,11 Z" fill="#000000" fillRule="nonzero" opacity="0.3" />
                              <path d="M0.00065168429,20.1992055 C0.388258525,15.4265159 4.26191235,13 8.98334134,13 C13.7712164,13 17.7048837,15.2931929 17.9979143,20.2 C18.0095879,20.3954741 17.9979143,21 17.2466999,21 C13.541124,21 8.03472472,21 0.727502227,21 C0.476712155,21 -0.0204617505,20.45918 0.00065168429,20.1992055 Z" fill="#000000" fillRule="nonzero" /> </g>
                          </svg>
                        </span>
                      </span>
                    </div>
                    <input onKeyDown={(e) => keyDown(e)}  className="form-control brdrLftNoRafius" onChange={(e) => {  setEmailError(false); e.preventDefault(); setEmail({ email: e.target.value }); }} type="text" />
                  </div>
                </div>
                <span id="wallsurlerror" className="errorMessage required"> {emailError ? "Kindly enter valid E-mail ID" : ""} </span>
                <span  id="<?php echo $identifier;?>emailIderror"  className="errorMessage required" ></span>
              </div>
            </div>
            <div className="collaborator_inner">
              <div className="row allPrmissins mb-5">
                <div className="col-md-4">
                  <div className="radio-inline">
                    <label className="radio radio-outline radio-primary d-flex align-items-center">
                      <input type="radio" name="data[Collaborator][collaboratorRole]" id="CollaboratorCollaboratorRole1" className="CollaboratorCollaboratorRole" value="1" onChange={() => { setValue("1"); setValue_1(false) }} style={{padding: "0.55rem 1rem"}} /> <span></span>Admin 
                    </label> 
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="radio-inline">
                    <label className="radio radio-outline radio-primary d-flex align-items-center">
                      <input  type="radio"  name="data[Collaborator][collaboratorRole]"  id="CollaboratorCollaboratorRole2" className="CollaboratorCollaboratorRole" value="2" onChange={() => { setValue("2"); setValue_1(false) }} />
                      <span></span>Editor </label>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="radio-inline">
                    <label className="radio radio-outline radio-primary d-flex align-items-center">
                      <input type="radio"  name="data[Collaborator][collaboratorRole]" id="CollaboratorCollaboratorRole3"  className="CollaboratorCollaboratorRole" value="3"  onChange={() => {  setValue("3");  setValue_1(false) }}  />  <span></span>Moderator  </label>
                  </div>
                </div>
                <span className="errorMessage required"> {value_1 ? "Please select at least one role" : ""} </span>
              </div>
              <div className="border-top collaborator-footer-action">
                <div className="col-12 text-right pt-4 px-0">
                  <button className={`btn btn-primary btn-md addCol ${spinner ? `spinner spinner-white spinner-right disabled` : null }`} onClick={(e) => onSubmit(e)}>
                    Add Collaborator
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      :
      <div className="col-lg-12 h-100">
        <div className="upgredeMain appsMore_empty text-center">
          <div className="py-8">
            <div className="Erroricon" style={{ display: 'none' }}><span className=""></span></div>
            <div className="Errorimage pb-3" ><img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/collabrate-icon.svg`} /></div>
            <div className="permissionMsg mt-4 px-4">
              <h4 className="ErrorMsg">Collaboration Feature</h4>
              <p className="msgSec">Provide access to more members of your team who can edit or customize your Gallery and develop stunning Gallery with teamwork.</p>
            </div>
            <div className="buttonUpgred mb-sm-5 mb-3">
              <a id="addonFeaturesScroll" onClick={() => { dispatch(togglePriceSection(true, 2)) }} className="commanBtn btn btn-primary px-5 d-inline-block">
                Upgrade Now
              </a>
            </div>
          </div>
        </div>
      </div>
  );
}
