import { Component } from "react"; 
import { DashboardModal, Dashboard } from '@uppy/react'
 
import $ from 'jquery'
import UppyUpload from "../../../../../utils/uppyUpload";

 
 
class Authorprofile extends Component {

    
    state = { filename: null, modalOpen: false, updateFile : 0, selected: "" }
    uppyUploader = new UppyUpload()
    componentDidMount() {  
        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ selectedFile: result.id, filename: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`, selected: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}` }, () => {
                setTimeout(() => {
                    this.uppyUploader.uppy.cancelAll()
                    this.props.onImgUpload(result.id,result.ext)
                    this.setState({ modalOpen: false })
                }, 300)
            })

        })
    }

    onClickToOpen = () => {

        this.setState({ modalOpen: true })
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll()
            this.setState({ modalOpen: false })
        })
    }
 
    setpostFile=(imageName)=>{ this.setState({ filename: imageName,updateFile : 1 }) }


    onClickToClose = () => { alert('a');
        this.uppyUploader.uppy.cancelAll()
        this.setState({ modalOpen: false })
    }

    componentWillUnmount() {
        this.uppyUploader.uppy.cancelAll()
        this.uppyUploader.uppy.close({ reason: 'unmount' })
        this.setState({ modalOpen: false })
    }


    render() {
    const { data,filename,updateFile, modalOpen, selectedFile } = this.state;
    const { imageName,onRemoveAuthor } = this.props

    if(imageName && updateFile==0){
       this.setpostFile(imageName);
    }


    return (

      <>

       {filename==null ? 
         <div className="symbol-label" id="authorImage">
            <img onClick={() => this.onClickToOpen()} className="align-self-center uppy-btn img-fluid" id="src-block" src="https://cloud.taggbox.com/taggshop/app/assets/media/users/author.png" alt="photo" />
          <input type="hidden" name="data[Post][authorFile]" autoComplete="off" className="upload authorFilePic" id="authorFile" />
        </div>
        :
       <div> 
        <div onClick={() => {this.setState({filename:null});onRemoveAuthor('')}}  id="removeAuthorImage" className="border btn-circle label bg-white btn-hover-text-primary btn-shadow customtopcorner removeAuthorImage" style={{right:'-5px',position:'absolute',top:'-10px', cursor:'pointer', zIndex:'5'}}>
            <i className="fa fa-times icon-xs"> </i>
        </div>

        <div className="symbol-label" id="authorImage">
            <img   className="align-self-center uppy-btn img-fluid" id="src-block"  src={filename}></img>
                <input type="hidden" name="data[Post][authorFile]" autoComplete="off" className="upload authorFilePic" id="authorFile" />
        </div>
        </div>  
    } 
 
        {/* {modalOpen ? ( */}
          <DashboardModal
            animateOpenClose={true}
            proudlyDisplayPoweredByUppy={false}
            inline={false}
            showProgressDetails={true}
            height={750}
            browserBackButtonClose={true}
            showRemoveButtonAfterComplete={false}
            uppy={this.uppyUploader.uppy}
            open={modalOpen}
            onRequestCloseModal={this.onClickToClose}
            closeAfterFinish={true}
            doneButtonHandler={this.doneButtonHandler}
            plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
          />
        {/* //) : null} */}
      </>
    );
  }
}

export default Authorprofile;
