import { Modal } from "react-bootstrap";
import { CLOUDPATH_TAGGSHOP, HOST_URL } from "../../../../../actions/api";
import $ from 'jquery';
import { useState } from "react";
import { togglePriceSection } from "../../../../../actions/show_modal_Action";
import { useDispatch } from "react-redux";
const UpgradeCard = ({ showcustomPost, closecustomPost }) => {
    const [customPost, setCustomPost] = useState(null);
    const dispatch = useDispatch()
    const onUpgradeClick = (e) => {
        setTimeout(() => {
            setCustomPost(null);

            window.showpricing(2, 'Commerce', 'commerce')
            closecustomPost()
        }, 100);

        $('#morePopupId').hide()
    }
    return (
        <div className="upgredeMain appsMore_empty text-center">
            <div className="py-8">
                <div className="Errorimage pb-3"><img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/custom-post-icon.svg`} /></div>
                <div className="permissionMsg mt-4 px-4">
                    <h4 className="ErrorMsg">Add Custom Posts</h4>
                    <p className="msgSec">Add posts that are private or unavailable on the feed. Customize your Gallery with engaging visuals using post URLs and upload option.</p>
                </div>
                <div className="buttonUpgred mb-sm-5 mb-3">
                    <a onClick={() => { dispatch(togglePriceSection(true, 2)) }} className="commanBtn btn btn-primary px-5 d-inline-block"  > Upgrade Now </a>
                </div>
            </div>
        </div>

    );
};

export default UpgradeCard;