import React, { useEffect, useState } from 'react'
import { Modal } from 'react-bootstrap'
import $ from 'jquery'
import { API_URL, APP_API_URL, CLOUDPATH_TAGGSHOP, HOST_URL } from '../../../../../actions/api';
import { useDispatch } from 'react-redux';
import { togglePriceSection } from '../../../../../actions/show_modal_Action';
export default function Api(props) {

  const dispatch = useDispatch()
  const wallKey = props.userData?.user_data?.user?.user_key;
  const [api, setApi] = useState(null);

  return (props.userData.user_data.UserRule.api ?
    <div className="modal-content">

      <div className="color-demo tooltips">
        <a href="javascript:void(0)" onClick={() => window.open(`${API_URL}/${localStorage.getItem('wallId')}?user_key=${wallKey}&format=json`, '', ' scrollbars=yes,menubar=no,width=300, resizable=yes,toolbar=no,location=no,status=no')}>
          <div className="color-view">
            <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/dashboard/icons/json-file1.png`} />
          </div>
        </a>
        <a href="javascript::()" onClick={() => window.open(`${API_URL}/${localStorage.getItem('wallId')}?user_key=${wallKey}&format=xml`, '', ' scrollbars=yes,menubar=no,width=300, resizable=yes,toolbar=no,location=no,status=no')}>
          <div className="color-view">
            <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/dashboard/icons/xml1.png`} />
          </div>
        </a>
      </div>
    </div> :

    <div className="upgredeMain text-center">
      <div className="Errorimage pb-3" ><img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/error/api-icon.svg`} /></div>
      <div className="permissionMsg mt-4 px-4">
        <h4 className="ErrorMsg">API Access</h4>
        <p className="msgSec">Get API access with our upgraded plan to integrate Tagshop gallery in your application and make your app stand out.</p>
      </div>
      <div className="buttonUpgred mb-sm-5 mb-3">
        <a href="javascript:;" id="payModalEditor" onClick={() => { dispatch(togglePriceSection(true, 2)) }} className="commanBtn btn btn-primary px-5 d-inline-block">
          Upgrade Now
        </a>
      </div>
    </div>
  )
}
