import React, { useEffect, useState } from 'react';
import { Modal } from 'react-bootstrap';
import { HOST_URL } from '../../../../../actions/api';
import { chooseLanguage, DeleteKeys } from '../../../../../actions/appsMoreAction';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS } from '../../../../../utils';
import { useNavigate } from 'react-router-dom';

export default function Chooselanguage(props) {
  const [poweredBy, setPoweredBy] = useState('')
  const [showMore, setShowMMore] = useState('')
  const [viewOn, setViewOn] = useState('')
  const [all, setAll] = useState('')
  const [state, setState] = useState()
  const [tab, setTab] = useState(false)
  const [spinner, setspinner] = useState(false);
  const language = ['English', 'French', 'Chinese', 'German', 'Korean', 'Malay', 'Italian', 'Dutch', 'Portuguese', 'Spanish']
  const navigate = useNavigate()
  useEffect(() => {
    setState(props.appsMore.detail?.wall?.Personalization?.w_language)
  }, [props.appsMore.detail?.wall?.Personalization?.w_language])

  useEffect(() => {
    if (props.appsMore.detail?.wall?.Personalization.custom_lan_data == '') return
    const customLanguageData = customLanguageParse(props.appsMore.detail?.wall?.Personalization.custom_lan_data).getCompleteData;
    setPoweredBy(customLanguageData.p_by)
    setShowMMore(customLanguageData.show_more)
    setViewOn(customLanguageData.view_on)
    setAll(customLanguageData.p_all)
  }, [props.appsMore.detail?.wall?.Personalization.custom_lan_data])

  const onSubmitData = (e) => {
    setspinner(true);
    if (e) {
      chooseLanguage({ lang: state }).then(() => {
        props.getNewData()
        DeleteKeys()
        TOAST_DISMISS()
        TOAST_SUCCESS('Custom Language saved successfully.');
  
       // window.location.href = `${HOST_URL}widget/wall/index/${localStorage.getItem('wallId')}`
        setspinner(false);
        navigate(`/content/moderation/${localStorage.getItem('wallId')}`)
        props.onHide();
      }).catch((err) => {
        TOAST_DISMISS()
       // TOAST_ERROR("Error");
        setspinner(false)
        props.onHide();
      })
    }
  }

  const onSubmitCustomLanguage = (e) => {

    e.preventDefault();

    if (poweredBy.trim().length != 0 && showMore.trim().length != 0 && viewOn.trim().length != 0 && all.trim().length != 0) {

      setspinner(true);

      let data = { w_language: 'custom', p_by: poweredBy, show_more: showMore, view_on: viewOn, p_all: all }

      if (e) {
        chooseLanguage(data).then(() => {
          DeleteKeys()
          props.getNewData()
          setspinner(false); 
          TOAST_SUCCESS('Custom Language saved successfully.');
          props.onHide()
         // window.location.href = `${HOST_URL}widget/wall/index/${localStorage.getItem('wallId')}`
        }
        ).catch(() => { 
        //  TOAST_ERROR('Error');
          setspinner(false)
          props.onHide()
        })
      }

    } else {
      TOAST_ERROR('Input invalid');
      return false
    }
  }

  const customLanguageParse = (data) => {
    let removeCha = data?.slice(4, data?.length)?.replace("{", "").replace("}", "");
    let splitWith = removeCha?.split(";");
    let getData = [];
    let getKey = [];
    let getCompleteData = {};
    splitWith?.map((item, index) => {
      let stringReplace = item?.replace('"', "").replace('"', "");
      let key = stringReplace?.slice(4, stringReplace.length);
      if (key != "") {
        let valueKey = key?.replace(":", "");
        if (index % 2 == 0) {
          getKey.push(valueKey);
        } else {
          getData.push(valueKey);
        }
      }
    });
    getKey.map((item, index) => {
      Object.assign(getCompleteData, { [item]: getData[index] });
    });

    return { getData, getKey, getCompleteData };
  };

  return ( 
          <div className="selectLanguage_wrap personalizePopup rightInner_wrap">
            <ul className="nav nav-tabs nav-tabs-space-lg nav-tabs-line nav-tabs-bold w-100" role="tablist">
              <li className="nav-item">
                <div className={`nav-link ${!tab ? `active` : ''}`} style={{ cursor: 'pointer' }} onClick={() => setTab(false)}>
                  <span className="nav-text">Language</span>
                </div>
              </li>
              <li className="nav-item">
                <div className={`nav-link ${tab ? `active` : ''}`} style={{ cursor: 'pointer' }} onClick={() => setTab(true)}>
                  <span className="nav-text">Custom</span>
                </div>
              </li>
            </ul>
            <div className="tab-content mt-8 hc-45">
              {!tab ? <div className="tab-pane fade show active h-100">
                <div className="language_inner h-100 d-flex flex-column justify-content-between">
                  <div className="row">
                    {language.map((language, index) =>
                      <div className="col-12 col-sm-6 col-lg-3 col-xl-3 col-xxl-2" key={index}>
                        <label onClick={() => setState(language)} className={`d-flex align-items-center mb-7 sel_lang ${state == language ? 'active' : ''}`}>
                          <div className="mr-3">
                            <img src={`https://cloud.taggbox.com/taggshop/app/assets/media/flags/${language}.png`} />
                          </div>
                          <div className="d-flex flex-column font-weight-bold">
                            <p className="text-dark font-size-lg">
                              {language}
                            </p>
                          </div>
                          <input type="radio" name="data[Personalization][w_language]" className="d-none language-input"/>
                        </label>
                      </div>)}
                  </div>
                  <div className="modal-footer px-0">
                    <div className="d-flex align-items-center justify-content-end w-100 m-0">
                      <div className="card-toolbar">
                        <button type="button" className="btn btn-secondary mr-4" onClick={() => props.onHide()}>Cancel</button>
                        <button type="submit" onClick={(e) => onSubmitData(e)} className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : null}`}>Apply</button>
                      </div>
                    </div>
                  </div>
                  <input name="data[Personalization][wallId]" value="<?php echo $wall['Personalization']['wallId'];?>" type="hidden" />
                </div>
              </div> :
                <div className="tab-pane fade show active h-100">
                  <form onSubmit={(e) => onSubmitCustomLanguage(e)} className='init noLoader h-100' >
                    <div className="clan h-100 d-flex flex-column justify-content-between">
                      <div className="chooseLanguage_wrap hc-90">
                        <input name="data[Personalization][w_language]" value="custom" type="hidden" />
                        <input name="data[Personalization][wallId]" value="<?php echo $wall['Personalization']['wallId'];?>" type="hidden" />
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <div className="form-group">
                              <label>Powered By <span className='ismandatory'>*</span></label>
                              <div className="input-group">
                                <input type="text" onChange={(e) => {
                                  setPoweredBy(e.target.value)
                                }} value={poweredBy} className="form-control" id="validationDefaultUsername" aria-describedby="inputGroupPrepend2" required />
                              </div>
                            </div>
                          </div>
                          <div className="col-12 col-sm-6">
                            <div className="form-group">
                              <label>Show More <span className='ismandatory'>*</span></label>
                              <div className="input-group">
                                <input type="text" onChange={(e) => {
                                  setShowMMore(e.target.value)
                                }} value={showMore} className="form-control" id="validationDefaultUsername" aria-describedby="inputGroupPrepend2" required />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-12 col-sm-6">
                            <div className="form-group">
                              <label>View On <span className='ismandatory'>*</span></label>
                              <div className="input-group">
                                <input type="text" onChange={(e) => {
                                  setViewOn(e.target.value)
                                }} value={viewOn} className="form-control" id="validationDefaultUsername" aria-describedby="inputGroupPrepend2" required />
                              </div>
                            </div>
                          </div>
                          <div className="col-12 col-sm-6">
                            <div className="form-group">
                              <label>All <span className='ismandatory'>*</span></label>
                              <div className="input-group">
                                <input type="text" onChange={(e) => {
                                  setAll(e.target.value)
                                }} value={all} className="form-control" id="validationDefaultUsername" aria-describedby="inputGroupPrepend2" required />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="form-actions fluid">
                        <div className="modal-footer px-0">
                          <div className="d-flex align-items-center justify-content-end w-100 m-0">
                            <div className="card-toolbar">
                              <button type="button" className="btn btn-secondary mr-4" onClick={() => props.onHide()}>Cancel</button>
                              <button type="submit" className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : null}`}>Apply</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>}
            </div>
          </div> 
  )
}
