import { Component } from "react";
import { DashboardModal, DragDrop, Dashboard } from '@uppy/react'
import $ from 'jquery'
import ReactHtmlParser from 'react-html-parser';
import UppyUpload from "../../../../../utils/uppyUpload";
import { VIDEO_COMPRESS } from "../../../../../actions/api";
import axios from "axios";
import { loadImage } from "../../moderation/components/view/grid/mediaWrapper";
import UppyUploadAll from "../../../../../utils/uppyUploadAll";

const requestHeaders = {
    productdomain: `taggbox.com`
}


class ImageUpload extends Component {
    state = {filename: null, modalOpen: false, mediaTyp: null, updateFile: 0, thumb_file: null, selected: ""}
 

    onBeforeUpload = (files) => {
        try {
            const fileKeys = Object.keys(files);

            if (files[fileKeys[0]].meta.type.includes("image")) {
                this.remove_StatusBar();
            }
            const updateFilesType = files[fileKeys[0]];

            if (updateFilesType.isRemote == false && fileKeys && fileKeys.length > 0 && files[fileKeys[0]] && files[fileKeys[0]].meta && files[fileKeys[0]].meta.type.includes("video")) {
                let progressindicators = document.querySelector(".uppy-Dashboard-progressindicators");
                var html_to_insert = '<div class="uppy-StatusBar is-postprocessing"><div class="uppy-StatusBar-progress is-indeterminate" role="progressbar" aria-valuemin="0" aria-valuemax="100" aria-valuenow="100" style="width: 100%;"></div><div class="uppy-StatusBar-content"><svg class="uppy-StatusBar-spinner" aria-hidden="true" focusable="false" width="14" height="14"><path d="M13.983 6.547c-.12-2.509-1.64-4.893-3.939-5.936-2.48-1.127-5.488-.656-7.556 1.094C.524 3.367-.398 6.048.162 8.562c.556 2.495 2.46 4.52 4.94 5.183 2.932.784 5.61-.602 7.256-3.015-1.493 1.993-3.745 3.309-6.298 2.868-2.514-.434-4.578-2.349-5.153-4.84a6.226 6.226 0 0 1 2.98-6.778C6.34.586 9.74 1.1 11.373 3.493c.407.596.693 1.282.842 1.988.127.598.073 1.197.161 1.794.078.525.543 1.257 1.15.864.525-.341.49-1.05.456-1.592-.007-.15.02.3 0 0" fill-rule="evenodd"></path></svg>Encoding...</div><div class="uppy-StatusBar-actions"><button type="button" class="uppy-u-reset uppy-StatusBar-actionCircleBtn" title="Cancel" aria-label="Cancel" data-uppy-super-focusable="true"><svg aria-hidden="true" focusable="false" class="uppy-c-icon" width="16" height="16" viewBox="0 0 16 16"><g fill="none" fill-rule="evenodd"><circle fill="#888" cx="8" cy="8" r="8"></circle><path fill="#FFF" d="M9.283 8l2.567 2.567-1.283 1.283L8 9.283 5.433 11.85 4.15 10.567 6.717 8 4.15 5.433 5.433 4.15 8 6.717l2.567-2.567 1.283 1.283z"></path></g></svg></button></div></div><div class="uppy uppy-Informer" aria-hidden="true"><p role="alert"> </p></div>';
                progressindicators.innerHTML = html_to_insert;
                $('.uppy-DashboardContent-back,.is-waiting,.uppy-Informer,.uppy-StatusBar-actions,.uppy-Dashboard-Item-action--remove').remove();
                this.setState({ isLoading: true })
                const updateFiles = files[fileKeys[0]];
                const data = new FormData();
                data.append("file", updateFiles.data);
                axios.post(VIDEO_COMPRESS, data, {
                    headers: requestHeaders
                }).then((response) => {
                    const { responseData } = response.data;
                    var imgheight = ""; var imgWidth = "";
                    if (responseData && responseData.length > 0) {
                        loadImage(responseData[0].s3ThumbUrl).then((image) => {
                        imgheight = image.height;
                        imgWidth = image.width;
                        let fname = `<iframe class="vimeoVideo iframeVideo" src="${responseData[0].s3Url}" width="100%" height="350" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>`
                        this.setState({ thumb_file: responseData[0].s3ThumbUrl, selectedFile: "", mediaTyp: 'video', filename: fname, selected: responseData[0].s3Url })
                        const { setmediaType, sethumbFile, setmediaWidth, setmediaHeight, setvideoType, setpostImage, setupdateFile } = this.props
                        setmediaType('video');
                        sethumbFile(responseData[0].s3ThumbUrl);
                        setmediaWidth(imgWidth);
                        setmediaHeight(imgheight);
                        setpostImage(fname);
                        setvideoType(0)
                        this.onClickToClose();
                        })

                    }
                }).catch((error) => {
                    this.setState({ thumb_file: "" })
                    progressindicators.innerHTML = '<div class="uppy-StatusBar is-postprocessing"><div class="uppy-error"><i class="fas fa-exclamation-circle"></i> something went wrong (Minimum video size more then 5 sec) </div></div>';
                })
                return false
            } else if (updateFilesType.isRemote == true) {
                $(".is-postprocessing").remove();
                const { sethumbFile } = this.props
                sethumbFile(updateFilesType.data.thumbnail ? updateFilesType.data.thumbnail : "");
            }
        } catch (error) {
            console.log('error', error)
        }
    }
    
    
    uppyUploader = new UppyUploadAll(this.onBeforeUpload)
    componentDidMount() {
        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            const fileKeys = Object.keys(result);
            this.setState({ selectedFile: result.id, mediaTyp: result.type, filename: result.type == 'video' ? `<video width="320" height="210" controls><source src="${`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`}" />  </video>` : `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`, selected: `https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}` }, () => {
                const { setmediaType, setmediaSize, setvideoType } = this.props
                setmediaType(result.type);
                setmediaSize(result.meta);
                if (result.type == 'video') {
                    let fname = `<video width="320" height="210" controls>
                    <source src="${`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`}" />
                   </video>`
                    const { setmediaWidth, setmediaHeight, setpostImage, setmediaFilePath } = this.props
                    setmediaWidth(result.meta.width);
                    setmediaHeight(result.meta.height);
                    setmediaFilePath(`https://cloud.tagshop.ai/tmp/${result.id}.${result.ext}`);
                    setpostImage(fname);
                    setvideoType(1);
                } else {
                    setvideoType(0);
                }
                setTimeout(() => {
                    this.uppyUploader.uppy.cancelAll()
                    this.props.onImgUpload(result.id, result.ext)
                    this.setState({ modalOpen: false })
                }, 300)
            })
        })
    }

    setpostFile = (imageName) => {
        this.setState({ filename: imageName, updateFile: 1 })
    }

    onClickToOpen = () => {
        this.setState({ modalOpen: true })
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll()
            this.setState({ modalOpen: false })
        })
        setTimeout(() => {
            this.updateProgressindicators();
        }, 10)
    }
    updateProgressindicators = () => {
        $(".uppy-Dashboard-progressindicators").remove();
    }
    remove_StatusBar = () => {
        $('.uppy-StatusBar').remove();
    }
    onClickToClose = () => {
        this.uppyUploader.uppy.cancelAll()
        this.setState({ modalOpen: false })
    }
    componentWillUnmount() {
        this.uppyUploader.uppy.cancelAll()
        this.uppyUploader.uppy.close({ reason: 'unmount' })
        this.setState({ modalOpen: false })
    }
    render() {
        const { data, filename, mediaTyp, updateFile, modalOpen, selectedFile } = this.state;
        const { imageName, postImage, onRemovePost, postType, setmediaType, mediaTypeFile } = this.props
        if (imageName && updateFile == 0) {
            this.setpostFile(imageName);
        }

        return (

            <div className="form-group">
                <div className="form-group custom-post_upload-image">
                    <div className="fileinput fileinput-new addProUploder uppy_upload">
                        <div className="fileinput-preview">
                            {filename == null ?
                                <div className="h-100 w-100 cursor-pointer uppy-btn" id="uppyModalOpenerCustom" onClick={() => this.onClickToOpen()}>
                                    <div className="d-flex align-items-center justify-content-center h-100 w-100">
                                        <svg id="Group_4957" data-name="Group 4957" xmlns="http://www.w3.org/2000/svg" width="81.564" height="66.999" viewBox="0 0 81.564 66.999">
                                            <path id="Path_5138" data-name="Path 5138" d="M249.771,382.533V320.066a2.268,2.268,0,0,1,2.266-2.266h77.033a2.268,2.268,0,0,1,2.266,2.266v62.468a2.268,2.268,0,0,1-2.266,2.266H252.036A2.268,2.268,0,0,1,249.771,382.533Z" transform="translate(-249.771 -317.8)" fill="#aec3ce"></path>
                                            <path id="Path_5139" data-name="Path 5139" d="M360.247,415.382h39.327a.8.8,0,0,0,.147-.012l-18.6-32.218a1.3,1.3,0,0,0-2.267,0l-12.483,21.621-.6,1.035Z" transform="translate(-328.254 -363.757)" fill="#fff"></path>
                                            <path id="Path_5140" data-name="Path 5140" d="M190.389,350.937h13.427l-6.591-9.574-.474-.69-.237-.345-.6,1.035Z" transform="translate(-158.396 -299.312)" fill="#aec3ce" opacity="0.2"></path>
                                            <path id="Path_5141" data-name="Path 5141" d="M284.634,438.674h34.015l-6.591-9.574-.474-.69-8.589-12.477a1.738,1.738,0,0,0-2.58-.154,1.544,1.544,0,0,0-.124.154Z" transform="translate(-274.538 -387.05)" fill="#fff"></path>
                                            <circle id="Ellipse_154" data-name="Ellipse 154" cx="5.826" cy="5.826" r="5.826" transform="translate(29.907 15.374)" fill="#fff"></circle>
                                        </svg>
                                    </div>
                                </div>
                                :
                                <><div data-provides="fileinput" id="imgUploadBtn">
                                    <div data-trigger="fileinput">
                                        {
                                            filename && filename != 'undefined' ?
                                                mediaTypeFile && mediaTypeFile != 'content' ?
                                                    mediaTypeFile == 'image' ? <img id="customPostThumb" className="img-fluid" src={filename} /> : ReactHtmlParser(filename)
                                                    :
                                                    postType == 3 || postType == 5 ?
                                                        ReactHtmlParser(filename)
                                                        :
                                                        <>
                                                            <img id="customPostThumb" className="img-fluid" src={filename} />
                                                        </>
                                                :
                                                <div className="d-flex align-items-center justify-content-center h-100 w-100"><svg id="Group_4957" data-name="Group 4957" xmlns="http://www.w3.org/2000/svg" width="81.564" height="66.999" viewBox="0 0 81.564 66.999"><path id="Path_5138" data-name="Path 5138" d="M249.771,382.533V320.066a2.268,2.268,0,0,1,2.266-2.266h77.033a2.268,2.268,0,0,1,2.266,2.266v62.468a2.268,2.268,0,0,1-2.266,2.266H252.036A2.268,2.268,0,0,1,249.771,382.533Z" transform="translate(-249.771 -317.8)" fill="#aec3ce"></path><path id="Path_5139" data-name="Path 5139" d="M360.247,415.382h39.327a.8.8,0,0,0,.147-.012l-18.6-32.218a1.3,1.3,0,0,0-2.267,0l-12.483,21.621-.6,1.035Z" transform="translate(-328.254 -363.757)" fill="#fff"></path><path id="Path_5140" data-name="Path 5140" d="M190.389,350.937h13.427l-6.591-9.574-.474-.69-.237-.345-.6,1.035Z" transform="translate(-158.396 -299.312)" fill="#aec3ce" opacity="0.2"></path><path id="Path_5141" data-name="Path 5141" d="M284.634,438.674h34.015l-6.591-9.574-.474-.69-8.589-12.477a1.738,1.738,0,0,0-2.58-.154,1.544,1.544,0,0,0-.124.154Z" transform="translate(-274.538 -387.05)" fill="#fff"></path><circle id="Ellipse_154" data-name="Ellipse 154" cx="5.826" cy="5.826" r="5.826" transform="translate(29.907 15.374)" fill="#fff"></circle></svg></div>
                                        }
                                    </div>
                                </div>
                                    <input type="hidden" name="data[Post][postFile]" autoComplete="off" className="upload posts0postFilen" id="posts0postFile" /></>
                            }
                        </div>

                        <div className="click_text" >
                            <div className="addIcoSec text-center my-4">
                                <div className="d-flex align-items-center justify-content-center mb-2">
                                    <button id="close-post-upload" onClick={() => this.onClickToOpen()} type="button" className="btn img_uploader_icon uppy-btn">
                                        <p className="svg-icon svg-icon-lg m-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                <g id="Group_4424" data-name="Group 4424" transform="translate(10922 -4685)">
                                                    <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10922 4685)" fill="none" opacity="0"></rect>
                                                    <path id="Path_5219" data-name="Path 5219" d="M-15.382,10.935h-6.172V4.765A1.065,1.065,0,0,0-22.618,3.7a1.065,1.065,0,0,0-1.064,1.064v6.17h-6.174a1.058,1.058,0,0,0-.751.317,1.056,1.056,0,0,0-.305.756,1.068,1.068,0,0,0,1.058,1.055h6.172v6.169a1.059,1.059,0,0,0,.311.754,1.021,1.021,0,0,0,.755.313,1.055,1.055,0,0,0,.749-.309,1.059,1.059,0,0,0,.313-.756V13.063h6.175a1.066,1.066,0,0,0,1.057-1.072A1.069,1.069,0,0,0-15.382,10.935Z" transform="translate(-10887.383 4685)" fill="#545454"></path>
                                                </g>
                                            </svg>
                                        </p>
                                    </button>
                                    <button onClick={() => { this.setState({ filename: null }); setmediaType(null); onRemovePost('') }} id="cp_remove_image" type="button" className="btn img_uploader_icon" style={{ display: filename ? 'block' : 'none' }}>
                                        <p className="svg-icon svg-icon-lg m-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                <g data-name="Group 4331" transform="translate(10956 -4533)">
                                                    <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                    <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                        <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                                                    </g>
                                                </g>
                                            </svg>
                                        </p>
                                    </button>
                                </div>
                                <div className="file_uploader_card_txt">
                                    <strong className="text-center text-muted mb-2">Click here</strong>
                                    <span>to upload the new image</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* {modalOpen ? ( */}

                <DashboardModal
                    animateOpenClose={true}
                    proudlyDisplayPoweredByUppy={false}
                    inline={false}
                    showProgressDetails={true}
                    height={750}
                    browserBackButtonClose={true}
                    showRemoveButtonAfterComplete={false}
                    uppy={this.uppyUploader.uppy}
                    open={modalOpen}
                    onRequestCloseModal={this.onClickToClose}
                    closeAfterFinish={true}
                    doneButtonHandler={this.doneButtonHandler}
                    plugins={["Instagram", "GoogleDrive", "Url", "Webcam"]}
                />
               {/* ) : null}  */}
            </div>
        );
    }
}

export default ImageUpload;
