import React, { useEffect, useState } from 'react'
import { Modal } from 'react-bootstrap';
import { HOST_URL } from '../../../../../actions/api';
import { DeleteKeys, hashtagHighlighter } from '../../../../../actions/appsMoreAction';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_LOADING, TOAST_SUCCESS } from '../../../../../utils';
import ColorComp from "./banner/elements/color";
import { useNavigate } from 'react-router-dom';

export default function HashtagHighlighter(props) {
  const [color, setColor] = useState(props.appsMore.hashtag_color) 
  const [value, setValue] = useState({ feedHastag: props.appsMore.hashtag_feed, allFeedHashtag: props.appsMore.hashtag_all }); 
  const [hashtag_highlight, setHashtag_highlight] = useState(props.appsMore.hashtag_highlight)
  const [chooseColor, setChooseColor] = useState(0);
  const [spinner, setspinner] = useState(false);
  const widgetTheme = props.appsMore?.widgetTheme
  const navigate = useNavigate()

  const onSubmitData = (e) => {
    const data = { hashtag_color: color, hashtag_feed: `${Number(value.feedHastag)}`, hashtag_all: `${Number(value.allFeedHashtag)}` , hashtag_highlight: `${Number(hashtag_highlight)}` }
    e.preventDefault();
    setspinner(true)
    TOAST_LOADING()
    if (e) {
      hashtagHighlighter(data).then(() => {
        props.getNewData()
        DeleteKeys()
        TOAST_DISMISS()
        TOAST_SUCCESS('Hashtag Highlighter saved successfully.');
        setspinner(false);
        navigate(`/content/moderation/${localStorage.getItem('wallId')}`)
        props.onHide() 
      }).catch((err) => {
        TOAST_DISMISS()
        TOAST_ERROR();
        setspinner(false)
        props.onHide()
      })
    }
  }

  return ( 
            <div className="tabbable tabbable-tabdrop hashtag_preview rightInner_wrap hc-45">
                    <div className="hashtagHighlighter_wrap hashcolor d-flex flex-column justify-content-between h-100">
                      <div className="row">
                        <div className="col-12">
                          <div className="form-group">
                            <label>Color</label>
                            <ColorComp id="hashtagHighliterHashtagColor" defaultColor={color} changeColor={(clr) => { setColor(clr) }} />
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form-group">
                            <div className="d-flex align-items-center justify-content-between">
                              <span>Feed Hashtags</span>
                              <div className="toggleOnBut">
                                <span className="switch justify-content-center"><label htmlFor="wallHashtagFeed"> 
                                  <input type="checkbox" id="wallHashtagFeed" className="onoffswitch-checkbox change_color feed_hashtag init" checked={value.feedHastag} data-gtm-form-interact-field-id="5" onChange={() => setValue({ ...value, feedHastag: !value.feedHastag })} /><span></span>
                                </label></span>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form-group">
                            <div className="d-flex align-items-center justify-content-between">
                              <span>All Hashtags</span>
                              <div className="toggleOnBut">
                                <span className="switch justify-content-center"><label htmlFor="wallHashtagAll">
                                  <input type="checkbox" id="wallHashtagAll" className="onoffswitch-checkbox change_color all_hashtag init" value="1" checked={value.allFeedHashtag}  onChange={() => setValue({ ...value, allFeedHashtag: !value.allFeedHashtag })} /><span></span>
                                </label></span>
                              </div>
                            </div>
                          </div>
                        </div> 
                        <div className="col-6">
                          <div className="form-group">
                            <div className="d-flex align-items-center justify-content-between">
                              <span>Status</span>
                              <div className="toggleOnBut">
                                <span className="switch justify-content-center"><label htmlFor="wallHashtagHighlight">
                                  <input type="checkbox" id="wallHashtagHighlight" className="onoffswitch-checkbox highlight_check init"  onChange={() => setHashtag_highlight(!hashtag_highlight)} checked={hashtag_highlight} /><span></span>
                                </label></span>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    <div className="modal-footer px-0 pb-0">
                      <div className="d-flex align-items-center justify-content-end w-100 m-0">
                        <div className="card-toolbar">
                          <button type="button" className="btn btn-secondary mr-4" onClick={() => props.onHide()}>Cancel</button>
                          <button type="submit" className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : null}`} onClick={e=> onSubmitData(e)}>Apply</button>
                        </div>
                      </div>
                    </div>
                  </div>
              
            </div > 
  )
}
