import React, { Component } from 'react'

export const loadImage = async src =>
    new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = (...args) => reject(args);
        img.src = src;
    });


class Media extends Component {
    constructor(props) {
        super(props);
        this.state = { height: '100', width: '100', error: '', imgUrl: props.ImageUrl }
    }

    componentWillMount() { this.onMediaLoad(this.props) }

    onMediaLoad = async (props) => {
        const { item } = props;
        const { imgUrl } = this.state;

        if (item && Object.keys(item).length > 0 && [2, 4, 5, 3].includes(item.type)) {
            if (item.mediaHeight && item.mediaHeight != "" && item.mediaWidth && item.mediaWidth != "") {
                this.setState({ height: (parseInt(item.mediaHeight) * 100) / parseInt(item.mediaWidth), width: (parseInt(item.mediaWidth) * 100) / parseInt(item.mediaHeight) })
            }
            else {
                const image = await loadImage(imgUrl);
                this.setState({ height: (image.height * 100) / image.width, width: (image.width * 100) / image.height })
            }

        }
    }
    onLoad = event => { if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src }) }
    componentWillReceiveProps(nextProps) {
        const { ImageUrl } = nextProps
        this.setState({ imgUrl: ImageUrl }, () => this.onMediaLoad(nextProps))
    }

    render() {
        const { children, item } = this.props
        const { height, imgUrl } = this.state
        return <div className="m_media_holder" style={{ paddingBottom: height + '%' }} data-height={item.mediaHeight ? '1' : '0'} data-original={item.newMedia} > {children} </div>
    }
}

export default Media;